import React, { useState, useEffect } from 'react';
import Container from '../shared/Containers/Container';
import Minibar from '../shared/UIElements/MiniBar/Minibar';
import { get_favorites } from '../shared/Functions/Firebase';
import { searchCallback } from '../shared/Functions/FirebaseCallbacks';
import GameCard from '../shared/UIElements/GameCard/GameCard';
import Spinner from '../shared/UIElements/Spinner/Spinner';
import './UserPage.css';

const UserPage = () => {

    const [state, setState] = useState({
        gamelist: [],
        showFav: true,
        noData: false,
        dataLoaded: false
    });

    const [imageState, setImageState] = useState({
        profileURL: "",
        coverURL: ""
    })

    useEffect(() => {
        loadImages();
        loadFavorites();
    }, [])

    const loadImages = () => {
        let userDetails = JSON.parse(localStorage.getItem("userCred"));
        let profileURL = userDetails.photoURL;

        setImageState({
            ...imageState,
            profileURL: profileURL
        })
    }

    const loadFavorites = () => {

        setState({
            ...state,
            noData: false,
            gamelist: [],
            showFav: true,
            dataLoaded: false
        })

        let userDetails = JSON.parse(localStorage.getItem("userCred"));
        let UID = userDetails.uid;

        get_favorites(UID, (snapshot) => {
            let array = searchCallback(snapshot);
            setState(prevState => {
                const newState = {
                    ...state,
                    gamelist: [...prevState.gamelist, ...array],
                    dataLoaded: true,
                }
                return newState;
            })
        })
        setTimeout(() => {
            setState(prevState => {
                if (!prevState.dataLoaded) {
                    const newState = {
                        ...prevState,
                        noData: true
                    }
                    return newState;
                }
                return prevState;
            })
        }, 5000)
    }

    let favorites = null;

    if (state.dataLoaded) {
        favorites = state.gamelist.map(item => {
            return <GameCard
                key={item.gameId}
                url={item.imageUrl}
                gameUrl={item.url}
                title={item.name}
                id={item.gameId}
                category={item.category}
            ></GameCard>
        })
    } else if (!state.dataLoaded && state.noData) {
        favorites = <h2 style={{ color: "red", margin: "auto", fontFamily: "sans-serif" }}>Sorry. No favorites found. Add favorites or <br />refresh to try again.</h2>
    } else {
        favorites = <Spinner />
    }

    return (
        <>
            <Container marginTop="0">
                <img alt="cover_image" className="Cover_img" src="https://images3.alphacoders.com/110/1104327.jpg"></img>
                <img alt="profile_img" className="Profile_img" src={imageState.profileURL}></img>
            </Container>
            <Minibar loadFav={loadFavorites} />

            {state.dataLoaded ? <h1 className="Userpage_h1">Favorites</h1> : null}

            <Container marginTop="10px">
                {
                    favorites
                }
            </Container>
        </>
    )
}

export default UserPage;